.map-container {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}


.hori-timeline .events {
    border-top: 3px solid #e9ecef;
    border-bottom: 3px solid #e9ecef;
}

.hori-timeline .events .event-list {
    display:block;
    position:relative;
    text-align:center;
    padding-top: 70px;
    margin-right: 0px
}

.hori-timeline .events .event-list:before {
    content: "";
    position:absolute;
    height: 36px;
    border-right: 2px dashed #dee2e6;
    top: 0;
}

.hori-timeline .events .event-list .event-date {
    position:absolute;
    top: 38px;
    left: 0;
    right: 0;
    width: 100%;
    margin: 0 auto;
}

@media (min-width: 1200px) {
    .hori-timeline .events .event-list {
        width: 20%;
        padding-top:45px;
    }
    .hori-timeline .events .event-list .event-date {
        top: -12px;
    }
    .hori-timeline .events {
        border-bottom: 0px;
    }
}

